}
.book-kode-v5 {
    font-size: 0.75rem;
    font-weight: 500;
    color: #a0b3c9;
    margin-bottom: 2px;
}
.book-info-v5 h4 {
    margin: 0 0 6px 0;
    font-size: 1rem;
    display:-webkit-box;
    -webkit-line-clamp:2;
    line-clamp:2;
    -webkit-box-orient:vertical;
    overflow:hidden;
}
.book-author-v5 {
    margin: 0 0 6px 0;
    font-size: 0.8rem;
    color: #e0e0e0;
    display:-webkit-box;
    -webkit-line-clamp:2;
    line-clamp:2;
    -webkit-box-orient:vertical;
    overflow:hidden;
}
.book-desc-v5 {
    margin: 0 0 8px 0;
    font-size: 0.8rem;
    color: #cfd8dc;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    overflow-wrap: break-word; /* broader support fallback */
    word-wrap: break-word; /* legacy alias */
    word-break: break-word;
    -webkit-hyphens: auto; /* iOS Safari support */
