}
.form-header-v5 h3 {
    margin: 0;
    color: var(--color-card-bg);
}
.close-button-v5 {
    background: none;
    border: none;
    font-size: 1.5rem;
    color: #95a5a6;
    cursor: pointer;
}
.form-group-v5 {
    margin-bottom: 15px;
}
.form-group-v5 label {
    display: block;
    font-weight: 600;
    margin-bottom: 5px;
    color: #34495e;
    font-size: 0.95rem;
}
.form-group-v5 input[type="date"],
.form-group-v5 textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
    box-sizing: border-box;
    transition: border-color 0.2s;
}
.form-group-v5 textarea {
    resize: vertical;
    min-height: 80px;
}
.form-group-v5 input:focus, .form-group-v5 textarea:focus {
    border-color: var(--color-action);
    outline: none;
}
.form-value-v5 {
    background-color: #ecf0f1;
    padding: 10px;
    border-radius: 6px;
    font-weight: 500;
    color: #2c3e50;
    margin-top: 0;
}
.form-group-v5 .required {
    color: var(--color-error);
    font-weight: 700;
    margin-left: 5px;
}
.submit-button-v5 {
    width: 100%;
    padding: 12px;
    background-color: var(--color-success);
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s;
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}
.submit-button-v5:hover {
    background-color: #27ae60;
}
.submit-button-v5:disabled {
    background-color: #bdc3c7;
    cursor: not-allowed;
}
.status-message-form.success {
    color: var(--color-success);
    background-color: #e6f7ec;
    padding: 8px;
    border-radius: 4px;
    margin-top: 15px;
    text-align: center;
}
.status-message-form.error {
    color: var(--color-error);
    background-color: #fcebeb;
    padding: 8px;
    border-radius: 4px;
    margin-top: 15px;
    text-align: center;
}
.note-v5 {
    font-size: 0.8rem;
    color: #7f8c8d;
    margin-top: 5px;
    margin-bottom: 0;
}

/* --- QR ACTION BUTTONS --- */
.qr-actions {
    display: flex;
    gap: 10px;
    margin-top: 12px;
    justify-content: center;
}
.qr-actions button {
    font-family: inherit;
    font-size: 0.75rem;
    padding: 8px 14px;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 6px;
    letter-spacing: 0.3px;
    transition: all .18s ease;
    border: 1px solid transparent;
}
.btn-copy-qr {
    background: #f3f4f6;
    color: #374151;
    border-color: #e5e7eb;
}
.btn-copy-qr:hover { background:#e5e7eb; }
.btn-copy-qr:active { transform: translateY(1px); }
.btn-download-qr {
    background: linear-gradient(90deg,#f8b739,#f59e0b);
    color: #fff;
    box-shadow: 0 2px 5px rgba(245,158,11,.4);
}
.btn-download-qr:hover { filter: brightness(1.05); }
.btn-download-qr:active { transform: translateY(1px); }
.qr-actions button:focus-visible {
    outline: 2px solid #2563eb;
    outline-offset: 2px;
}
.qr-wrapper svg { box-shadow: 0 4px 10px rgba(0,0,0,.15); border-radius: 12px; }
.note-v5.important {
    color: var(--color-error);
    font-weight: 500;
    text-align: center;
    border: 1px solid var(--color-error);
    padding: 8px;
    border-radius: 4px;
    margin-top: 15px;
}


