}

/* Unified container width similar to homepage */
.borrowing-page-container-v5, .book-list-container-v5, .pending-loans-container-v5, .book-detail-container-v5 { 
    max-width:1200px; 
    margin:0 auto; 
}
.qr-content-box {
    padding: 20px;
    border: 1px dashed #ccc;
    border-radius: 8px;
    margin-bottom: 20px;
}
.loan-code-display {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--color-error);
    margin-bottom: 15px;
}
.qr-placeholder {
    /* Placeholder for actual QR code image/component */
    width: 100%;
    height: 200px;
    background-color: #f0f0f0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    font-size: 0.9rem;
    color: #7f8c8d;
    margin-bottom: 10px;
}
.qr-placeholder p {
    padding: 0 10px;
}

/* Utility & Refresh */
.loading-bar, .no-data {
    text-align: center;
    padding: 20px;
    color: #555;
    font-style: italic;
}
.status-message {
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 15px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 8px;
}
.status-message.error {
    background-color: #f8d7da;
    color: #721c24;
}
.status-message.success {
    background-color: #d4edda;
    color: #155724;
}
.refresh-button-v5 {
    background: none;
    border: 1px solid #ccc;
    color: #555;
    padding: 8px 15px;
    border-radius: 20px;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 8px;
    margin: 10px auto 20px 0; /* Posisikan di kiri atas content */
}
.refresh-button-v5:hover {
    background-color: #f0f0f0;
    border-color: #999;
}

/* Ready Return UI */
.ready-return-wrapper { margin-top: 10px; }
.ready-return-button {
    width: 100%;
    padding: 10px;
    background: linear-gradient(135deg,#2ecc71,#27ae60);
    color:#fff;
    border:none;
    border-radius:6px;
    font-weight:600;
    cursor:pointer;
    font-size:.9rem;
    box-shadow:0 3px 8px rgba(46,204,113,.4);
    transition:all .2s;
}
.ready-return-button:hover { filter:brightness(1.05); }
.ready-return-button:disabled { background:#95a5a6; box-shadow:none; cursor:not-allowed; }
.status-ready-info { margin-top:12px; font-size:.8rem; font-weight:600; color:#27ae60; text-align:center; }
.status-ready-info.pending-verif { color:#f59e0b; }
.hidden-file-input { display:none; }
/* Gunakan teknik visually hidden agar iOS/Android mengizinkan pemicu input file via label */
.visually-hidden-file { position:absolute; left:-9999px; width:1px; height:1px; opacity:0; }

/* Success popup after proof upload */
.proof-success-modal { background:#fff; padding:25px; border-radius:14px; width:90%; max-width:360px; text-align:center; position:relative; box-shadow:0 10px 25px rgba(0,0,0,.25); }
.proof-success-modal h3 { margin-top:0; color:#2c3e50; }
.proof-success-modal p { font-size:.9rem; color:#555; }
.proof-success-check { width:80px; height:80px; border-radius:50%; background:linear-gradient(135deg,#2ecc71,#27ae60); display:flex; align-items:center; justify-content:center; margin:0 auto 15px; box-shadow:0 4px 10px rgba(39,174,96,.4); }
.proof-success-check svg { font-size:2.5rem; color:#fff; }
.proof-success-check.danger { background: linear-gradient(135deg,#ef4444,#dc2626); box-shadow:0 4px 10px rgba(239,68,68,.35); }
.close-success-btn { margin-top:15px; background:#2ecc71; color:#fff; border:none; padding:10px 18px; border-radius:8px; font-weight:600; cursor:pointer; box-shadow:0 3px 8px rgba(46,204,113,.4); }
.close-success-btn:hover { filter:brightness(1.05); }
.approval-check { background:linear-gradient(135deg,#59D3F8,#1F3C6D); }
.approval-actions { display:flex; gap:10px; justify-content:center; }
.btn-secondary-gray { background:#95a5a6; }


/* --- MEDIA QUERIES (Responsif) --- */
@media (min-width: 600px) {
    .borrowing-page-container-v5 {
        min-height: 90vh;
        max-width: 900px; 
    }
    .header-content-v5 {
        padding: 30px 40px;
    }
    .search-box-v5 {
        max-width: 450px;
    }
    /* Removed old flex overrides for book cards to keep CSS grid layout intact */
    /* If needed, adjust grid column min width via earlier media queries instead of forcing flex */
    .book-cover-v5 {
        width: 90px;
        height: 120px;
    }
    .book-info-v5 h4 {
        font-size: 1.2rem;
    }
    /* Removed legacy flex overrides for loan list; grid definitions earlier handle responsiveness */
    .loan-tabs-v5 button {
        font-size: 1rem;
        padding: 15px 10px;
    }
}

/* ================= Pay Fine Modal ================= */
.pay-fine-modal-overlay { z-index: 1200; }
.pay-fine-modal { background:#fff; border-radius:20px; padding:28px 30px 26px; width: min(520px, 92%); max-height:80vh; overflow-y:auto; box-shadow:0 20px 50px -10px rgba(0,0,0,.25); position:relative; animation:fadeInScale .35s ease; }
.pay-fine-modal h3 { margin-top:0; font-size:1.4rem; font-weight:700; }
.fine-items-list { margin:12px 0 14px; display:flex; flex-direction:column; gap:10px; }
.fine-item-row { background:#f5f7fa; border:1px solid #e2e8f0; padding:10px 14px; border-radius:14px; display:flex; justify-content:space-between; align-items:flex-start; }
.fine-item-info { flex:1; }
.fine-book-title { font-size:.9rem; font-weight:600; margin:0 0 4px; line-height:1.2; }
.fine-amount { font-size:.85rem; color:#e53935; font-weight:600; margin:0; display:flex; align-items:center; gap:6px; }
.fine-total-box { background:#0f5132; color:#fff; border-radius:14px; padding:14px 18px; display:flex; justify-content:space-between; align-items:center; font-size:.95rem; margin:4px 0 16px; }
.fine-total-box strong { font-size:1.05rem; letter-spacing:.5px; }
.pay-fine-actions { display:flex; justify-content:flex-end; gap:12px; margin-top:4px; }
.btn-primary-pay { background:linear-gradient(90deg,#16a34a,#17b159); color:#fff; border:none; padding:12px 22px; border-radius:12px; cursor:pointer; font-weight:600; font-size:.9rem; display:inline-flex; align-items:center; gap:6px; box-shadow:0 4px 12px -2px rgba(23,177,89,.4); }
.btn-primary-pay:disabled { opacity:.45; cursor:not-allowed; }
.btn-secondary-gray { background:#e2e8f0; color:#1f2937; border:none; padding:11px 20px; border-radius:12px; cursor:pointer; font-weight:500; font-size:.85rem; }
.btn-secondary-gray:hover:not(:disabled) { background:#d4dbe3; }
.btn-primary-pay:hover:not(:disabled) { filter:brightness(1.05); }
.fine-hint { font-size:.65rem; color:#6b7280; margin:14px 0 0; text-align:center; }
.modal-msg { font-size:.7rem; margin-top:-4px; }
@keyframes fadeInScale { from { opacity:0; transform:translateY(12px) scale(.96); } to { opacity:1; transform:translateY(0) scale(1); } }

/* Payment Method Section */
.payment-method-box { border:1px solid #e5e7eb; border-radius:16px; padding:14px 16px 18px; background:#fafafa; margin-top:4px; }
.pm-label { font-size:.7rem; text-transform:uppercase; letter-spacing:.8px; font-weight:600; color:#374151; margin:0 0 8px; }
.pm-options { display:flex; gap:10px; margin-bottom:10px; flex-wrap:wrap; }
.pm-option { flex:1 1 auto; background:#fff; border:1px solid #d1d5db; padding:9px 10px; border-radius:10px; font-size:.7rem; font-weight:600; cursor:pointer; display:flex; align-items:center; gap:6px; justify-content:center; color:#374151; transition:.18s all; }
.pm-option.active { background:#1f5133; color:#fff; border-color:#1f5133; box-shadow:0 4px 10px -2px rgba(31,81,51,.4); }
.pm-option:not(.active):hover { background:#f1f5f9; }
.pm-icon { font-size:.9rem; }
.pm-detail { background:#fff; border:1px dashed #cbd5e1; padding:12px 14px 10px; border-radius:12px; font-size:.7rem; line-height:1.1rem; color:#374151; }
.pm-bank-info p, .pm-cash-box p { margin:2px 0; }
.pm-hint { font-size:.6rem; color:#6b7280; margin:4px 0 0; }
.pm-qris-box { display:flex; flex-direction:column; align-items:center; }
.pm-qris-placeholder { width:170px; height:170px; border:2px dashed #1f2937; border-radius:16px; display:flex; flex-direction:column; align-items:center; justify-content:center; font-size:.65rem; font-weight:600; gap:6px; background:repeating-linear-gradient(45deg,#f8fafc,#f8fafc 10px,#f1f5f9 10px,#f1f5f9 20px); }
.pm-qris-placeholder .qris-icon { font-size:2.4rem; color:#111827; }
.proof-note { font-size:.62rem; background:#fffbe6; border:1px solid #fcd34d; padding:8px 10px; border-radius:10px; margin:8px 0 6px; color:#92400e; line-height:.95rem; }
/* Selection additions */
.fine-select-all { background:#eef4ff; border:1px solid #cbd5e1; }
.fine-select-label { font-size:.7rem; font-weight:600; display:flex; align-items:center; gap:8px; }
.fine-checkbox-wrapper { display:flex; align-items:flex-start; padding-top:2px; margin-right:8px; }
.fine-checkbox-wrapper input { width:16px; height:16px; cursor:pointer; }
.fine-item-row.dimmed { opacity:.55; filter:saturate(.4); }
.selection-hint { font-size:.6rem; color:#374151; margin:-6px 0 8px; text-align:right; font-style:italic; }
.sr-only { position:absolute; width:1px; height:1px; padding:0; margin:-1px; overflow:hidden; clip:rect(0,0,0,0); border:0; }

/* ===== History Fine Filter (Final Placement) ===== */
.history-filter-bar {
    display: flex;
    gap: 8px;
    align-items: center;
    margin: 10px 0 4px;
    flex-wrap: wrap;
    font-size: 0.8rem;
}
.history-filter-bar span { font-weight: 600; color:#1f2937; }
.history-filter-btn {
    background:#f3f4f6;
    border:1px solid #e5e7eb;
    padding:4px 10px;
    border-radius:16px;
    cursor:pointer;
    font-size:0.7rem;
    letter-spacing: .25px;
    transition:background .15s, color .15s, border-color .15s;
